"use strict";

/* Dropdown */
const slide = (() => {

    var totalItems = $('.carousel-item').length;
    var currentIndex = $('div.active').index() + 1;
    $('.num').html(''+currentIndex+'/'+totalItems+'');

    $('#carouselControls1').on('slid.bs.carousel', function() {
        currentIndex = $('div.active').index() + 1;
       $('.num').html(''+currentIndex+'/'+totalItems+'');
    });
})();

export default slide;


